.settingsContainer {
    background-color: rgb(24, 24, 24);
}

.settingsContainerChild {
    background-color: black;
    padding-right: 1em;
    padding-left: 1em;
    padding-bottom: 3em;
}

.header {
    color: rgb(232, 230, 227);
    text-align: center;
}

.listContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(11, 12, 13);
    padding: 2em;
    margin-left: 10vw;
    margin-right: 10vw;
    transition: all .1s;
}

.editContainer:last-child {
    border-bottom: 2px solid black;
}

.editContainer:hover {
    background-color: rgb(30, 30, 30);
}

.editContainer2 {
    background-color: rgb(30, 30, 30);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 2px;
    text-align: center;
    border: 2px solid black;
    border-bottom: 1px solid black;
    min-width: 50vw;
}

.item2 {
    color: rgb(232, 230, 227);
    font-weight: bold;
    font-size: 1.4em;
    padding: 1em;
}

.edit {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1em;
    padding: .5em;
    border-radius: .1em;
    background-image: linear-gradient(to right, rgb(0, 216, 108), rgb(42, 125, 163));
    cursor: pointer;
    transition: all .5s;
}

.edit:hover {
    color: rgb(209, 208, 206);
    background-image: linear-gradient(to right, rgb(0, 161, 81), rgb(34, 100, 131));
}

.edit:active {
    color: rgb(185, 185, 185);
    background-image: linear-gradient(to right, rgb(0, 150, 75), rgb(29, 86, 112));
}

.delete {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(189, 22, 0);
    padding: .5em;
    border-radius: .1em;
    cursor: pointer;
}

.delete:hover {
    background-color: rgb(150, 17, 0);
}

.delete:active {
    background-color: rgb(124, 14, 0);
}

.imageLoading {
    width: 3em;
    display: flex;
    justify-self: center;
    align-self: center;
    margin-top: 3em;
}

@media screen and (max-width: 996px) {
    .listContainer {
        margin: 0;
    }

    .item2 {
        font-size: 1em;
    }

    .item {
        font-size: 1em;
    }
}

@media screen and (max-width: 778px) {
    .settingsContainerChild {
        margin: 0;
        padding: 0;
    }

    .listContainer {
        padding-right: 0;
        padding-left: 0;
    }

    .buttonBox {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .edit {
        margin: 0;
        margin-bottom: 1em;
        border-radius: .5em;
    }

    .delete {
        border-radius: .5em;
        margin: 0;
    }
}

@media screen and (max-width: 648px) {
    .item2 {
        font-size: .8em;
    }

    .item {
        font-size: .8em;
    }
}


@media screen and (max-width: 480px) {
    .item2 {
        font-size: .7em;
    }

    .item {
        font-size: .7em;
    }
}