.editContainer {
    background-color: rgb(24, 24, 24);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 2px;
    text-align: center;
    border: 2px solid black;
    border-bottom: 1px solid black;
    min-width: 50vw;
    user-select: none;
}

.item {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(232, 230, 227);
    font-weight: normal;
    font-size: 1.1em;
    padding: 1em;
}

.edit {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1em;
    padding: .5em;
    border-radius: .1em;
    background-image: linear-gradient(to right, rgb(0, 216, 108), rgb(42, 125, 163));
    cursor: pointer;
    transition: all .5s;
}

.edit:hover {
    color: rgb(209, 208, 206);
    background-image: linear-gradient(to right, rgb(0, 161, 81), rgb(34, 100, 131));
}

.edit:active {
    color: rgb(185, 185, 185);
    background-image: linear-gradient(to right, rgb(0, 150, 75), rgb(29, 86, 112));
}

.delete {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(189, 22, 0);
    padding: .5em;
    border-radius: .1em;
    cursor: pointer;
}

.delete:hover {
    background-color: rgb(150, 17, 0);
}

.delete:active {
    background-color: rgb(124, 14, 0);
}

@media screen and (max-width: 996px) {
    .item {
        font-size: 1em;
    }
}

@media screen and (max-width: 778px) {
    .buttonBox {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .edit {
        margin: 0;
        margin-bottom: 1em;
        border-radius: .5em;
    }

    .delete {
        border-radius: .5em;
        margin: 0;
    }
}

@media screen and (max-width: 648px) {
    .item {
        font-size: .8em;
    }
}


@media screen and (max-width: 480px) {
    .item {
        font-size: .7em;
    }
}