.posterContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(24, 24, 24);
    padding: 1em;
    border-radius: .5em;
    text-decoration: none;
}

.poster {
    width: 15em;
    height: auto;
    border-radius: 1em;
    cursor: pointer;
    transition: all .2s;
}

.poster:hover {
    scale: 1.02;
}

.loadingPoster {
    margin: 10em;
    width: 5em;
    height: auto;
}

.linker {
    text-decoration: none;
    text-align: center;
}

.posterHeader {
    color: rgb(232, 230, 227);
    font-weight: bold;
    font-size: 1.3em;
    text-align: center;
    cursor: pointer;
}

@media screen and (max-width: 778px) {
    .posterHeader {
        font-size: 1.1em;
    }
}